.filter-section {
  margin-bottom: 24px;
  padding-bottom: 25px;
  border-bottom: 1px solid #d1d8e1;
}
.filter-section__count {
  margin-left: 15px;
  font-size: 12px;
  color: #94a0b1;
}
.filter-section_noborder {
  margin-bottom: 25px;
  padding-bottom: 0;
  border-bottom: 0;
}
.filter-section__scroll {
  max-height: 195px;
}
.filter-section_open .filter-section__title {
  margin-bottom: 16px;
}
.filter-section_open .filter-section__title:before {
  background-color: #bac2cd;
  -webkit-transform: translate(0, -50%) rotate(0deg);
  -ms-transform: translate(0, -50%) rotate(0deg);
  transform: translate(0, -50%) rotate(0deg);
}
.filter-section_open .filter-section__title:after {
  background-color: #bac2cd;
}
.filter-section_open .filter-section-toggle {
  display: block;
}
.filter-section_notitle .filter-section-toggle {
  display: block;
}
.filter-section__title {
  margin: 0 0 0 0;
  padding: 0 25px 0 0;
  display: block;
  position: relative;
  font-size: 16px;
  font-weight: 500;
  color: #1a1a1a;
  text-decoration: none;
}
.filter-section__title:after {
  content: '';
  width: 14px;
  height: 2px;
  position: absolute;
  top: 50%;
  right: 5px;
  background: currentColor;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
}
.filter-section__title:before {
  content: '';
  width: 14px;
  height: 2px;
  position: absolute;
  top: 50%;
  right: 5px;
  background: currentColor;
  -webkit-transition: -webkit-transform 200ms;
  transition: -webkit-transform 200ms;
  -o-transition: transform 200ms;
  transition: transform 200ms;
  transition: transform 200ms, -webkit-transform 200ms;
  -webkit-transform: translate(0, -50%) rotate(90deg);
  -ms-transform: translate(0, -50%) rotate(90deg);
  transform: translate(0, -50%) rotate(90deg);
  -webkit-transform-origin: center;
  -ms-transform-origin: center;
  transform-origin: center;
}
@media (hover: hover) {
  .filter-section__title:hover {
    text-decoration: none;
    opacity: 0.8;
  }
}
.filter-section-toggle {
  display: none;
}
.filter-section__body {
  padding: 0;
  background: #fff;
}
.filter-section__body .field {
  margin-bottom: 14px;
}
.filter-section__body .field-input-radio,
.filter-section__body .field:last-child {
  margin-bottom: 0;
}
.filter-section-search {
  padding: 0 0 16px 0;
  position: relative;
}
.filter-section-search-btn {
  width: 20px;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 5px;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
}
.filter-section-search .ico-cat-search {
  position: absolute;
  width: 14px;
  height: 14px;
  top: 50%;
  left: 50%;
  color: #94a0b1;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.filter-section-search__input {
  padding: 0 15px 0 35px;
  width: 100%;
  height: 39px;
  background: #f4f4f8;
  font-size: 14px;
  border: none;
  border-radius: 6px;
  text-align: left;
}
.filter-section-search__input:focus {
  outline: none;
}
.filter-section-search__input::-webkit-input-placeholder {
  color: #94a0b1;
}
.filter-section-search__input::-ms-input-placeholder {
  color: #94a0b1;
}
.filter-section-search__input::placeholder {
  color: #94a0b1;
}
.wrap-filter-section-search__input {
  position: relative;
}
.filter-section__scroll .simplebar-track.simplebar-vertical {
  width: 4px;
  border-radius: 4px;
}
.filter-section__scroll .simplebar-track {
  background: #f4f4f8;
  border-radius: 5px;
}
.filter-section__scroll .simplebar-scrollbar:before {
  background: #2858ff;
  border-radius: 5px;
}
.wrap-filter-selected {
  margin-bottom: 20px;
}
.filter-selected {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
}
.filter-selected__body {
  margin-bottom: -9px;
}
.filter-selected__item {
  margin: 0 8px 9px 0;
  padding: 13px 36px 13px 16px;
  height: auto;
  display: inline-block;
  font-size: 14px;
  position: relative;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  line-height: 1;
  color: #1a1a1a;
  background: #f4f4f8;
  border-radius: 25px;
  text-decoration: none;
  -webkit-transition: border 0.2s ease-out;
  -o-transition: border 0.2s ease-out;
  transition: border 0.2s ease-out;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
@media (hover: hover) {
  .filter-selected__item:hover {
    color: #2858ff;
    text-decoration: none;
  }
}
.filter-selected__item_act {
  color: #2858ff;
  background: #fff;
}
.ico-filter-selected-close {
  width: 10px;
  height: 10px;
  position: absolute;
  top: 50%;
  right: 18px;
  display: inline-block;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
}
