@import '../../scss/global/variables.scss';
@import '../../scss/global/mixins.scss';

.select-field {
  position: relative;

  select {
    width: 100%;
  }

  label {
    position: absolute;
    top: 23px;
    left: 16px;
    pointer-events: none;
    transition: font-size 0.2s, top 0.2s, color 0.2s;
  }

  select:not([data-empty]) {
    & ~ label {
      @include b2;
      top: 12px;
      color: $grey400;
    }
  }

  select[data-selected] {
    & ~ label {
      @include b2;
      top: 12px;
      color: $grey400;
    }
  }

  select:disabled {
    & ~ label {
      color: $grey300;
    }
  }

  .select2 {
    width: 100% !important;
  }

  .select2-selection {
    outline: none;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      display: none;
      height: calc(64px + 8px);
      width: calc(100% + 8px);
      transform: translate(-4px, -4px);
      border: 2px solid $primary;
      border-radius: 9px;
      pointer-events: none;
    }

    &.focused {
      &::after {
        display: block;
      }
    }
  }

  .select2-container .select2-selection--single {
    background-color: $grey200;
    border: none;
    border-radius: 5px;
    height: auto;
    transition: background-color 0.2s;

    @include hover {
      background-color: $grey250;
    }
  }

  .select2-container .select2-selection--single .select2-selection__rendered {
    padding-left: 16px;
    padding-right: 40px;
    padding-bottom: 13px;
    padding-top: 30px;
    min-height: 64px;
    @include b1;
  }

  .select2-container--default .select2-selection--single .select2-selection__arrow {
    width: 24px;
    height: 24px;
    top: calc(50% - 12px);
    right: 16px;

    @include bg-svg(
      '<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 10L12 14L16 10" stroke="rgba(108, 118, 132, 1)" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/></svg>'
    );

    b {
      display: none;
    }
  }

  .select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow {
    transform: rotate(180deg);
  }

  .select2-container.select2-container--disabled .select2-selection--single {
    background-color: $grey200;

    .select2-selection__arrow {
      @include bg-svg(
        '<svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 10L12 14L16 10" stroke="rgba(209, 216, 225, 1)" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/></svg>'
      );
    }
  }

  .select2-container.select2-container--disabled .select2-selection--single .select2-selection__rendered {
    color: $grey300;
  }
}

.select2-dropdown {
  border: none;
  border-radius: 5px;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.1);

  &.select2-dropdown-first-empty {
    .select2-results__option:first-child {
      display: none;
    }
  }

  .select2-results__option {
    padding: 12px 16px;
    color: $main-text;
    @include b2;
  }

  .select2-results__option--highlighted.select2-results__option--selectable {
    background-color: $grey200;
    color: $main-text;
  }

  .select2-results__option--selected {
    background-color: $grey200;
  }
}
