.swiper {
  &.with-scroll-indicator {
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      width: 72px;
      height: 72px;
      margin-top: -36px;
      background-image: url('../img/touch-slider.svg');
      background-size: contain;
      background-repeat: no-repeat;
      z-index: 2;
      pointer-events: none;

      @include desktop-m {
        display: none;
      }
    }
  }
}
