.page-main {
  position: relative;
  padding-top: 60px;

  @include tablet {
    padding-top: 120px;
  }

  @include mobile {
    padding-top: vmin(104);
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 24px;
    height: 794px;
    background: gradient1();

    @include desktop-m {
      display: none;
    }
  }

  &__header {
    @include tablet {
      margin: 0 0 40px;
    }

    @include mobile {
      margin-bottom: vmin(24);
    }
  }

  &__header-container {
    display: flex;
    align-items: flex-start;
  }

  &__logo-link {
    flex-shrink: 0;

    img {
      width: 260px;
      height: auto;

      @include desktop-m {
        width: 221px;
      }
    }

    @include tablet {
      display: none;
    }
  }

  &__header-text {
    max-width: 555px;
    margin-left: auto;
    text-align: right;

    @include desktop-m {
      max-width: 575px;
    }

    @include tablet {
      max-width: none;
      display: flex;
      justify-content: space-between;
    }

    @include mobile {
      flex-direction: column;
      max-width: 100%;
      width: 100%;
    }
  }

  &__title {
    text-align: right;
    margin: 0 0 16px;

    @include desktop-m {
      font-size: 40px;
    }

    @include tablet {
      width: 54%;
      font-size: 32px;
      text-align: left;
      margin: 0;
    }

    @include mobile {
      width: 100%;
      font-size: vmin(24);
      margin-bottom: vmin(8);
    }
  }

  &__header-desc {
    @include b1;
    max-width: 500px;
    margin-left: auto;

    @include tablet {
      text-align: left;
      width: calc(46% - 20px);
    }

    @include mobile {
      width: 100%;
      font-size: vmin(12);
      max-width: none;
    }
  }
}
