.brands {
  &__inner {
    position: relative;
    display: flex;
    height: 540px;

    @include desktop-m {
      height: 43.94vw;
    }

    @include mobile {
      height: vmin(590);
    }
  }

  &__bg {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;

      @include mobile {
        object-position: bottom center;
      }
    }
  }

  &__content {
    position: absolute;
    top: 0;
    left: 0;
    width: 55%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 80px;

    @include desktop-m {
      padding: 6.25vw;
    }

    @include mobile {
      width: 100%;
      padding: vmin(36) vmin(24);
      text-align: center;
    }
  }

  &__title,
  &__subtitle {
    color: #fff;
  }

  &__title {
    @include mobile {
      margin-left: auto;
      margin-right: auto;
    }
  }

  &__subtitle {
    b {
      display: inline-block;
      position: relative;

      span {
        position: relative;
        z-index: 2;
      }

      &::before {
        content: '';
        width: 100%;
        height: 12px;
        position: absolute;
        bottom: 4px;
        left: 0;
        background: linear-gradient(90deg, #5ea9ff 0%, #d5d3ff 100%);
      }
    }

    @include tablet {
      font-size: 24px;

      b {
        &::before {
          height: 7px;
          bottom: 3px;
        }
      }
    }

    @include mobile {
      &:not(.brands__subtitle--mobile) {
        display: none;
      }

      font-size: vmin(24);
      margin-bottom: vmin(16);
      max-width: vmin(260);
      margin-left: auto;
      margin-right: auto;

      b {
        &::before {
          height: vmin(7);
          bottom: vmin(2);
        }
      }
    }
  }

  &__link {
    margin-top: auto;

    @include mobile {
      margin-top: 0;
      width: 100%;
    }
  }
}
