@import '../../scss/global/variables.scss';
@import '../../scss/global/mixins.scss';
@import '../../scss/global/functions.scss';

.button,
.button-secondary,
.button-third,
.button-gradient-1,
.button-gradient-2,
.button-gradient-3,
.button-gradient-4,
.button-gradient-5,
.button-gradient-6,
.button-gradient-7 {
  @include h7;
  line-height: 19px;
  display: inline-block;
  padding: 17px 24px 15px;
  background-color: $primary;
  border: none;
  color: #fff;
  text-align: center;
  vertical-align: middle;
  transition: background-color 0.2s, color 0.2s;
  border-radius: 8px;
  outline: none;

  & * {
    vertical-align: middle;
  }

  @include mobile {
    font-size: vmin(12);
    line-height: 120%;
    padding: vmin(17) vmin(24) vmin(15);
    border-radius: vmin(8);
  }

  &.focused {
    position: relative;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: calc(100% + 8px);
      height: calc(100% + 8px);
      border: 2px solid $primary;
      transform: translate(-4px, -4px);
      border-radius: 12px;
      pointer-events: none;

      @include mobile {
        border-radius: calc(vmin(8) + 4px);
      }
    }
  }
}

.button {
  @include desktop-only {
    @include hover {
      cursor: pointer;
      background-color: $additional2;
    }

    &:active {
      background-color: $additional2;
    }
  }

  &:disabled,
  &.disabled {
    cursor: default;
    background-color: $grey300;
    pointer-events: none;
  }
}

.button-gradient-1,
.button-gradient-2,
.button-gradient-3,
.button-gradient-4,
.button-gradient-5,
.button-gradient-6,
.button-gradient-7 {
  background-position: left center;
  background-size: 200% 100% !important;
  transition: background-position 0.2s;

  @include hover {
    cursor: pointer;
    background-position: right center;
  }

  &:disabled,
  &.disabled {
    cursor: default;
    background: none;
    background-color: $grey300;
    pointer-events: none;
  }
}

.button-gradient-1 {
  background: linear-gradient(to right, $gradient1-start 0%, $gradient1-end 50%, $gradient1-start 100%);
}

.button-gradient-2 {
  background: linear-gradient(to right, $gradient2-start 0%, $gradient2-end 50%, $gradient2-start 100%);
  // background: gradient2(to right);
}

.button-gradient-3 {
  background: linear-gradient(to right, $gradient3-start 0%, $gradient3-end 50%, $gradient3-start 100%);
  // background: gradient3(to right);
}

.button-gradient-4 {
  background: linear-gradient(to right, $gradient4-start 0%, $gradient4-end 50%, $gradient4-start 100%);
  // background: gradient4(to right);
}

.button-gradient-5 {
  background: linear-gradient(to right, $gradient5-start 0%, $gradient5-end 50%, $gradient5-start 100%);
  // background: gradient5(to right);
}

.button-gradient-6 {
  background: linear-gradient(to right, $gradient6-start 0%, $gradient6-end 50%, $gradient6-start 100%);
  // background: gradient6(to right);
}

.button-gradient-7 {
  background: linear-gradient(to right, $gradient7-start 0%, $gradient7-end 50%, $gradient7-start 100%);
  // background: gradient7(to right);
}

.button-secondary,
.button-third {
  background-color: $grey200;
  color: $main-text;

  @include desktop-only {
    @include hover {
      cursor: pointer;
      background-color: $grey250;
    }

    &:active {
      background-color: $grey250;
    }
  }

  &:disabled,
  &.disabled {
    cursor: default;
    background-color: $grey200;
    color: $grey300;
    pointer-events: none;
  }
}

.button-third {
  background-color: #fff;

  @include desktop-only {
    @include hover {
      background-color: $grey200;
    }

    &:active {
      background-color: $grey200;
    }
  }
}

.button-arrow {
  @include h7;
  padding: 0;
  background-color: transparent;
  border: none;
  display: inline-flex;
  align-items: center;
  color: $primary;
  transition: color 0.2s;

  svg {
    width: 24px;
    height: 24px;
    margin-left: 8px;
    transform: translateY(-1px);
  }

  @include hover {
    cursor: pointer;

    color: $additional2;
  }

  &:active {
    color: $additional2;
  }

  &:disabled,
  &.disabled {
    color: $grey300;
    pointer-events: none;
  }

  &.focused {
    position: relative;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: calc(100% + 6px);
      height: calc(100% + 4px);
      border: 2px solid $primary;
      transform: translate(-3px, -2px);
      border-radius: 4px;
      pointer-events: none;
    }
  }

  @include mobile {
    font-size: vmin(16);

    svg {
      width: vmin(24);
      height: vmin(24);
      margin-left: vmin(8);
    }
  }
}

.button--small {
  @include h8;
  line-height: 14px;

  @include mobile {
    font-size: vmin(12);
    line-height: vmin(14);
  }
}
