@import '../../scss/global/variables.scss';
@import '../../scss/global/mixins.scss';

.company-map {
  position: relative;
  // overflow: hidden;
  display: flex;
  // height: 688px;
  width: 100%;
  max-width: 100%;
  background-position: center;
  background-size: 100% auto;
  background-attachment: fixed;
  // background-position: top vmin(-90) center;

  @include mobile {
    background-attachment: unset;
  }

  &__underlayer {
    position: fixed;
    top: 0;
    left: 50%;
    height: 100%;
    width: 100%;
    max-width: 100%;
    overflow: hidden;
    z-index: -100;

    img {
      position: absolute;
      top: 50%;
      left: 0;
    }
  }

  &__overlayer {
    // position: absolute;
    // top: 0;
    // left: 0;
    width: 100%;
    max-width: 100%;
    height: auto;
  }

  &__places {
    @include tablet {
      display: none;
    }
  }

  &__place {
    position: absolute;
  }

  &__pin-btn {
    width: 24px;
    height: 24px;
    padding: 0;
    color: $main-text;

    svg {
      width: 24px;
      height: 24px;
      transition: color 0.2s, transform 0.2s;
      transform-origin: 50% 100%;
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 100%;
      left: 50%;
      width: 36px;
      height: 16px;
      background-size: contain;
      background-position: top center;
      background-repeat: no-repeat;
      transform: translateX(-50%);
      @include bg-svg(
        '<svg width="32" height="16" viewBox="0 0 32 16" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M31.5561 0.32959L15.9997 15.8859L0.443359 0.32959H31.5561Z" fill="white"/></svg>'
      );
      z-index: 3;
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.2s ease-in-out, visibility 0s ease-in-out;
      transition-delay: 0s, 0.2s;
    }

    @include hover {
      cursor: pointer;

      svg {
        color: $fail;
        transform: scale(1.2);
      }
    }
  }

  &__popup {
    position: absolute;
    bottom: calc(100% + 15px);
    left: 50%;
    padding: 24px;
    background-color: #fff;
    width: 250px;
    transform: translateX(-50%);
    filter: drop-shadow(0px 4px 20px rgba(0, 0, 0, 0.15));
    z-index: 2;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.2s ease-in-out, visibility 0s ease-in-out;
    transition-delay: 0s, 0.2s;
  }

  &__place {
    &.opened {
      .company-map__popup {
        opacity: 1;
        visibility: visible;
        transition-delay: 0s;
      }

      .company-map__pin-btn {
        &::after {
          opacity: 1;
          visibility: visible;
          transition-delay: 0s;
        }
      }
    }
  }

  &__popup-close {
    position: absolute;
    top: 24px;
    right: 24px;
    width: 24px;
    height: 24px;
    padding: 0;
    color: $grey400;
    transition: color 0.2s;

    svg {
      width: 24px;
      height: 24px;
    }

    @include hover {
      color: $main-text;
      cursor: pointer;
    }
  }

  &__popup-header {
    width: 130px;
    display: inline-block;
    text-align: right;
    margin-bottom: 24px;
    // max-width: 150px;

    img {
      width: 129px;
      min-width: 129px;
      height: auto;
    }
  }

  &__popup-city {
    display: block;
    width: 100%;
    @include h8;
    margin-top: 4px;
  }

  &__popup-items {
    margin-bottom: 24px;
  }

  &__popup-item {
    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &__popup-label {
    @include b3;
    color: $grey400;
    margin: 0 0 4px;
    display: block;
  }

  &__popup-text {
    @include b2;
    display: block;

    a {
      color: $main-text;
      transition: color 0.2s;

      @include hover {
        color: $grey400;
      }
    }
  }

  &__popup-bottom {
    text-align: right;
  }

  &__popup-about-link {
    @include h8;
    color: $main-text;
    transition: color 0.2s;

    @include hover {
      color: $grey400;
    }
  }
}
